import {adaptiveDivArr} from './adaptiveBlock.js'

window.addEventListener('resize', () => {
    if(adaptiveDivArr.length > 0) {
        adaptiveDivArr.forEach(adapt => {
            adapt.resize();
        });
    } 
    document.querySelector('.modal.show')?.classList.remove('show');
    document.querySelector('.modal-link-active')?.classList.remove('modal-link-active')
    document.body.classList.remove('lock');
})